import {
  Component,
  OnInit,
  ChangeDetectionStrategy,
  OnDestroy
} from '@angular/core';
import { EventDto, ReasonDto } from '@gb/models';
import { Observable, of, Subscription } from 'rxjs';
import { Store, Select } from '@ngxs/store';
import {
  SelectedEventState,
  BeginEdit
} from '@gb/gb-common/state/edit/event.state';
import { Router, ActivatedRoute } from '@angular/router';
import { DictionariesState } from '@gb/gb-common/state/gloabal/dictioanries.state';
import { trigger, state, style, transition, animate } from '@angular/animations';

@Component({
  selector: 'gb-event-editor',
  template: `
    <p *ngIf="(loading$ | async)">Loading event...</p>
    <gb-event-editor-form
      *ngIf="!(loading$ | async)"
      [event]="event$ | async"
      [reasons]="eventReasons$ | async | tag"
    ></gb-event-editor-form>
  `,
  
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class EventEditorComponent implements OnInit, OnDestroy {
  @Select(SelectedEventState.loading) loading$: Observable<boolean>;
  @Select(SelectedEventState.event) event$: Observable<EventDto>;
  @Select(SelectedEventState.reasons) eventReasons$: Observable<ReasonDto[]>;

  routeSubscr: Subscription;

  constructor(
    private store: Store,
    private router: Router,
    private route: ActivatedRoute
  ) {
    this.routeSubscr = route.paramMap.subscribe(p => {
      const id = p.get('id');
      if (!id) {
        return;
      } else {
        store.dispatch(new BeginEdit(id));
      }
    });
  }

  ngOnDestroy() {
    if (this.routeSubscr) {
      this.routeSubscr.unsubscribe();
      this.routeSubscr = null;
    }
  }

  ngOnInit() { }
}
