import { Pipe, PipeTransform } from '@angular/core';
import { ReasonDto } from '@gb/models';
import { Tag } from '../components';

@Pipe({
  name: 'tag'
})
export class TagPipe implements PipeTransform {
  transform(values: ReasonDto[], args?: any): Tag[] {
    if (!values) { return null; }

    console.log(values);
    return values.map(v => new Tag(v.name, 'primary', v.id));
  }
}
