import { environment } from './environment';

class Api {
    public static base = 'api'

    public static $(route: string) {
        if (environment.production)
            return route;

        return route.replace(new RegExp('/', 'g'), '-');
    }

    public static Events = {
        base: Api.base + "/events",
        get(id: string) { return Api.Events.base + `/${id}`; },
        getReasons(id: string) { return Api.Events.base + Api.$(`/${id}/reasons`); }
    };

    public static Dict = {
        base: Api.base + "/dict",
        getReasons() { return Api.Dict.base + Api.$("/reasons"); }
    };
}

export let API = Api;