import { ReasonDto } from '@gb/models';
import { State, Selector, Actions, Action, StateContext, createSelector } from '@ngxs/store';
import { ReasonsService } from '@gb/services';

export class DictionariesModel {
    loading?: boolean;
    reasons: ReasonDto[];
}

export class LoadReasons {
    public static type = '[Dictionaries] LoadReasons';
}

@State<DictionariesModel>({
    name: 'globalDictionaries',
    defaults: {
        loading: null,
        reasons: null
    }
})
export class DictionariesState {

    constructor(private reasonsSvc: ReasonsService) {}

    @Selector()
    static reasons(state: DictionariesModel) {
        return state.reasons;
    }

    @Selector()
    static reasonsExcept(state:DictionariesModel){
        return (ids:string[]) =>{
           return  state.reasons.filter(s=> !ids.findIndex(i=>i===s.id));
        }
        
    }

    @Action(LoadReasons)
    loadReasons(ctx: StateContext<DictionariesModel>) {

        this.reasonsSvc.getAll()
            .subscribe(data => {
                ctx.setState({
                    loading: false,
                    reasons: data
                });
                console.log(data);

            }
         );
    }

}
