import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ReasonDto } from '@gb/models';
import { API } from '../../../environments/api-routes';


@Injectable({
  providedIn: 'root'
})
export class ReasonsService {
  constructor(private http: HttpClient) {}

  getAll() {
    return this.http.get<ReasonDto[]>(API.Dict.getReasons());
  }
}
