import { Component, OnInit, Input } from '@angular/core';
import { Observable, of } from 'rxjs';

export class Tag {
  constructor(
    public name: string,
    public color: string = 'primary',
    public id: string = null) { }

    toString() {
      return name;
    }
}

@Component({
  selector: 'gb-tag-control',
  template:
`<div class="gb-control">
    <ng-container *ngFor="let tag of tags">
      <span style="margin-right: 3px;" class="badge bg-{{tag.color}} incrased-font">
        {{tag.name}}
        <span *ngIf="!isReadonly" style="cursor: pointer" aria-hidden="true" (click)="removeTag(tag)">&#x2717;</span>
      </span>
    </ng-container>
    <div class="w-100"></div>
    <div class="input-group col-12 col-sm-6 offset-sm-3  pl-0">

    <ng-container *ngIf="mode=='typeahead'">
        <input #inputTag  type="text" class="form-control form-control-sm"
          placeholder="" aria-label="Recipient's username" aria-describedby="button-addon2" >
          <div class="input-group-append">
            <button class="btn btn-sm btn-outline-success" type="button" id="button-addon2"
              (click)="addTag(inputTag, null)">&#x2795;</button>
          </div>
    </ng-container>

    <ng-container *ngIf="mode=='select'">
      <select #selectedTag  class="form-control form-control-sm"
        placeholder="" aria-label="Recipient's username" aria-describedby="button-addon2">
        <option [value]="null"></option>
        <option *ngFor="let val of dataSource" [value]="val.id">{{val.name}}</option>
      </select>
      <div class="input-group-append">
            <button class="btn btn-sm btn-outline-success" type="button" id="button-addon2"
              (click)="addTag(null, selectedTag)">&#x2795;</button>
          </div>
    </ng-container>
      
    </div>
  </div>
  `,
  styles: ['']
})
export class TagControlComponent implements OnInit {

  @Input()
  tags: Tag[] = [];

  @Input()
  dataSource: Tag[] = [];

  @Input()
  mode: 'select'|'typeahead';
  constructor() { }

  ngOnInit() {
  }

  removeTag(tag: Tag) {
    this.tags = this.tags.filter(t => t.id !== tag.id);

    if (this.dataSource.findIndex(v => v.id === tag.id) < 0) {
      this.dataSource = [...this.dataSource, tag].sort((a, b) => a.name > b.name ? 1 : -1);
    }
  }

  addTag(inputTag?: HTMLInputElement, selectedTag?: HTMLSelectElement) {
    let addedTag: Tag = null;

    if (inputTag) {
      throw new Error('Not implemented!');
    } else if (selectedTag) {
      const id =  selectedTag.value ;
      addedTag = this.dataSource.find((v: Tag) => v.id === id);
    }

    if (!addedTag) {
      return;
    }

    const foundTag = this.tags.findIndex(v => v.id === addedTag.id);
    if (foundTag > -1) {
      this.tags[foundTag].color = 'warning';
    } else {
      this.tags = [...(this.tags ? this.tags : []), new Tag(addedTag.name, 'warning', addedTag.id)];
    }

    this.dataSource = this.dataSource.filter(v => v.id !== addedTag.id);
  }

}
