import { EventDto, ReasonDto } from '@gb/models';
import { State, Selector, Action, StateContext } from '@ngxs/store';
import { of } from 'rxjs';
import { EventsService } from '@gb/services/events.service';

export class SelectedEventModel {
  loading: boolean;
  event: EventDto;
  reasons?: ReasonDto[];
}

export class BeginEdit {
  static readonly type = '[Event] BeginEdit';
  constructor(public id: string) { }
}

@State<SelectedEventModel>({
  name: 'selectedEvent',
  defaults: {
    event: null,
    loading: true
  }
})
export class SelectedEventState {
  constructor(private eventSvc: EventsService) { }

  @Selector()
  static event(state: SelectedEventModel) { return state.event; }

  @Selector()
  static reasons(state: SelectedEventModel) { return state.reasons; }


  @Selector()
  static loading(state: SelectedEventModel) { return state.loading; }

  @Action(BeginEdit)
  editEvent(ctx: StateContext<SelectedEventModel>, action: BeginEdit) {
    const state = ctx.getState();
    ctx.setState({ event: null, loading: true });

    this.eventSvc
      .get(action.id)
      .pipe(


      )
      .subscribe((e: EventDto) => {
        let newState = ctx.setState({ ...state, event: e, loading: false });

        this.eventSvc
          .getReasons(action.id)
          .subscribe((data: ReasonDto[]) =>
            ctx.patchState({ reasons: data})
          );

      }
      );



  }
}
