import { Component, OnInit, Input, ChangeDetectionStrategy, Output, EventEmitter } from '@angular/core';
import { EventDto, StateDto } from '@gb/models';



@Component({
    selector: 'gb-event-table',
    changeDetection: ChangeDetectionStrategy.OnPush,
    template: `

  <table class="table table-striped ">
      <thead class="thead-inverse">
          <tr>
              <th>#</th>
              <th>Поступило</th>
              <th>Время</th>
              <th>Место</th>
              <th>Статус</th>
          </tr>
      </thead>
      <tbody>

          <tr *ngFor="let row of events;let i=index" (click)="onRowClick(row)">
            <td scope="row">{{i}}</td>
            <td>{{row.originalDate | date:'dd.MM.yy'}}</td>
            <td>Morning</td>
            <td>{{row.place}}</td>
            <td>{{row.state}}</td>
          </tr>

      </tbody>
  </table>

  `,
    styleUrls: ['./event-table.component.css']
})
export class EventTableComponent implements OnInit {

    @Input()
    public events: EventDto[] = [];

    @Output()
    public selected = new EventEmitter<EventDto[]>();

    constructor() { }

    ngOnInit() {
    }

    public onRowClick(row: EventDto) {
        this.selected.emit([row]);
    }

}
