import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { EventDto, ReasonDto } from '@gb/models';
import { Observable } from 'rxjs';
import { API } from '../../environments/api-routes';

@Injectable({
  providedIn: 'root'
})
export class EventsService {
  constructor(private http: HttpClient) {    
  }

  get(id: string) {
    return this.http.get<EventDto>(API.Events.get(id));
  }

  find(arg0: any): Observable<EventDto[]> {
    return this.http.get<EventDto[]>(API.Events.base);
  }

  getReasons(id:string){
    return this.http.get<ReasonDto[]>(API.Events.getReasons(id));
  }

}
