import { InMemoryDbService } from 'angular-in-memory-web-api';
import { EventDto, ReasonDto } from '@gb/models';


export class TestDataService implements InMemoryDbService {

  constructor() { }
  createDb() {
    const dict = {
      reasons: [
        new ReasonDto('id-r-1', 'reason1'),
        new ReasonDto('id-r-2', 'reason2'),
        new ReasonDto('id-r-3', 'reason3'),
        new ReasonDto('id-r-4', 'reason4'),
        new ReasonDto('id-r-5', 'reason5')
      ]
    };

    const events = [
      new EventDto('id-0', 'email', new Date(2018, 12, 22), 'morning', 'Subject 1', '<b>Body of the event 1</b>', 'Vasya', 'new'),
      new EventDto('id-1', 'email', new Date(2018, 12, 21), 'morning', 'Subject 2', '<b>Body of the event 2</b>', 'Vasya', 'new'),
      new EventDto('id-2', 'email', new Date(2018, 12, 20), 'morning', 'Subject 3', '<b>Body of the event 3</b>', 'Vasya', 'new'),
      new EventDto('id-3', 'email', new Date(2018, 12, 19), 'morning', 'Subject 4', '<b>Body of the event 4</b>', 'Vasya', 'new'),
      new EventDto('id-4', 'email', new Date(2018, 12, 18), 'morning', 'Subject 5', '<b>Body of the event 5</b>', 'Vasya', 'new')      
    ];

    return {
      'events': events,

      // fake collection for route "api/events/{id}/reasons"
      'events-id-0-reasons': dict.reasons.slice(0,1),
      'events-id-1-reasons': dict.reasons.slice(0,2),
      'events-id-2-reasons': dict.reasons.slice(1,2),
      'events-id-3-reasons': dict.reasons.slice(0,4),
      'events-id-4-reasons': dict.reasons.slice(3,5),


      'dict-reasons': dict.reasons
    };
  }
}
