import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { GoogleChartsModule } from 'angular-google-charts';
import { NgModule } from '@angular/core';
import { NgbModule, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { FormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule } from '@angular/router';
import { CommonModule } from '@angular/common';
import { AppComponent } from './app.component';
import { SomePageComponent } from './some-page';
import { AppRoutingModule } from './app-routing.module';
import { GbCommonModule, ModalComponent } from './gb-common';
import { EventEditorComponent, OccasionsModule } from './occasions';
import { NgxsModule } from '@ngxs/store';
import { HttpClientInMemoryWebApiModule } from 'angular-in-memory-web-api';
import { TestDataService } from './test/data.service';
import { NgxsLoggerPluginModule } from '@ngxs/logger-plugin';
import { SelectedEventState } from './gb-common/state/edit/event.state';
import { environment } from '../environments/environment';
import { DictionariesState } from './gb-common/state/gloabal/dictioanries.state';


@NgModule({
  declarations: [
    AppComponent,
    SomePageComponent
  ],
  imports: [
    NgxsModule.forRoot([SelectedEventState, DictionariesState], { developmentMode: !environment.production }),
    NgxsLoggerPluginModule.forRoot(),
    HttpClientModule,
    HttpClientInMemoryWebApiModule.forRoot(TestDataService, { delay: 500 }),
    NgbModule,
    BrowserModule.withServerTransition({ appId: 'ng-cli-universal' }),
    BrowserAnimationsModule,
    GoogleChartsModule.forRoot(),
    CommonModule,
    FormsModule,

    AppRoutingModule,
    GbCommonModule,
    OccasionsModule
  ],
  exports: [GbCommonModule, NgbModule],
  providers: [NgbActiveModal],
  entryComponents: [ModalComponent, EventEditorComponent],
  bootstrap: [AppComponent, ]
})
export class AppModule { }
