import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { OccasionsRoutingModule } from './occasions-routing.module';
import { EventTableComponent } from './event-table';
import { EventEditorComponent, TextPresenterComponent } from './event-editor';
import { GbCommonModule } from '@gb/gb-common';
import { EventEditorFormComponent } from './event-editor/event-editor-form/event-editor-form.component';
import { NgbDatepicker, NgbDatepickerModule } from '@ng-bootstrap/ng-bootstrap';


@NgModule({
  imports: [
    CommonModule,
    OccasionsRoutingModule,
    GbCommonModule,
    NgbDatepickerModule
  ],
  declarations: [EventTableComponent, EventEditorComponent, TextPresenterComponent, EventEditorFormComponent],
  exports: [EventTableComponent, EventEditorComponent],
  entryComponents: [EventEditorComponent]
})
export class OccasionsModule { }
